body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#base {
  background-color: white;
  padding: 0;
}
#navbar {
  margin-top: var(--header-size);
  margin-bottom: 50px;
}
#container {
  padding: 0px 3% 0px 3%;
}

.gray_background {
  background-color: rgb(232, 237, 223);
  padding: 5px 10px 5px 10px;
  border-radius: 5px;
  box-shadow: 1px 2px 1px 1px var(--green-5);
}

.header1 {
  font-size: 40px;
  margin: 5px 5px 5px 0;
}
.header2 {
  font-size: 30px;
  margin: 5px 5px 5px 0;
}
.header3 {
  font-size: 20px;
  margin: 5px 5px 5px 0;
}

.tab {
  padding-left: 3ch;
}
.bold {
  font-weight: bold;
}
.underline {
  text-decoration: underline;
}

.order-decimal {
  list-style-type: decimal;
}
.order-lower-alpha {
  list-style-type: lower-alpha;
}
.order-disc {
  list-style-type: disc;
}

:root {
  --green-1: #89a236;
  --green-2: #d7e7a3;
  --green-3: #aec566;
  --green-4: #667f13;
  --green-5: #475d00;
  --purple-1: #7c2969;
  --purple-2: #b17da5;
  --purple-3: #964e86;
  --purple-4: #610e4f;
  --purple-5: #470037;
  --header-size: 75px;
}
